@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@300&display=swap');

/*Fondo de pantalla:*/

body {
    background: linear-gradient(
      90deg,
      rgba(48, 16, 255, 1) 0%,
      rgba(100, 115, 255, 1) 100%
    );
    text-align: center;
}

/*Configuración creador*/

.creator{
    font-family: 'Raleway', sans-serif;
    color: #fff;
}

/*Contenedor principal*/

.container-form{
    background-color: #000;
    width: 50%;
    min-height: 600px;
    margin: 0 auto;
    margin-top: 50px;
    padding: 20px 20px;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    text-align: center;
}

/*Configuración del título de la aplicación*/

.title-form{
    color: #fff;
    font-family: 'Raleway', sans-serif;
    font-size: 30px;
}

/*Subcontenedor para el input form y el botón*/

.sub-container{
    margin-top: 25px;
    margin-bottom: 25px;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

/*Configuración del botón*/

.btn{
    font-family: 'Raleway', sans-serif;
}

/*Configuración del input-form*/

.form-control{
    width: 50%;
    margin-right: 10px;
    font-family: 'Raleway', sans-serif;
}

/*Configuración de los item-list (cambio de colores)*/

.item-list{
    background: linear-gradient(
    90deg,
    rgba(255, 118, 20, 1) 0%,
    rgba(255, 84, 17, 1) 100%);
    width: 70%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 0 auto;
    margin-bottom: 15px;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 10px;
    border-radius: 5px;
    font-family: 'Raleway', sans-serif;
    color: #fff;
    font-size: 20px;
    font-weight: bold;
    text-align: left;
}

.item-list:nth-child(4n + 1) {
    background: linear-gradient(
      90deg,
      rgba(93, 12, 255, 1) 0%,
      rgba(155, 0, 250, 1) 100%
    );
}
  
.item-list:nth-child(4n + 2) {
    background: linear-gradient(
      90deg,
      rgba(255, 12, 241, 1) 0%,
      rgba(250, 0, 135, 1) 100%
    );
}
  
.item-list:nth-child(4n + 3) {
    background: linear-gradient(
      90deg,
      rgba(20, 159, 255, 1) 0%,
      rgba(17, 122, 255, 1) 100%
    );
}

.container-icon{
    margin-right: 10px;
}

.fa-times-circle{
    font-size: 15px;
    margin-right: 10px;
}

.fa-pencil-alt{
    font-size: 15px;
}

/*Configuración para dispósitivos móviles*/

@media (max-width: 550px){

.container-form{
    width: 90%;
}

.item-list{
    width: 100%;
}

.form-control{
    width: 100%;
    margin-bottom: 10px;
}

.btn{
    width: 50%;
}

.sub-container{
    flex-direction: column;
    align-items: center;
}

}